.tu-form-item {
  margin-bottom: 22px; }
  .tu-form-item::before,
  .tu-form-item::after {
    display: table;
    content: ""; }
  .tu-form-item::after {
    clear: both; }
  .tu-form-item__label-wrap {
    float: left; }
    .tu-form-item__label-wrap .tu-form-item__label {
      display: inline-block;
      float: none; }
  .tu-form-item__label {
    text-align: right;
    vertical-align: middle;
    float: left;
    font-size: 14px;
    color: #8394be;
    line-height: 40px;
    padding: 0 12px 0 0;
    box-sizing: border-box; }
  .tu-form-item__content {
    line-height: 40px;
    position: relative;
    font-size: 14px; }
    .tu-form-item__content::before,
    .tu-form-item__content::after {
      display: table;
      content: ""; }
    .tu-form-item__content::after {
      clear: both; }
    .tu-form-item__content .tu-input-group {
      vertical-align: top; }
  .tu-form-item__error {
    color: #f56c6c;
    font-size: 12px;
    line-height: 1;
    padding-top: 4px;
    position: absolute;
    top: 100%;
    left: 0; }
    .tu-form-item__error--inline {
      position: relative;
      top: auto;
      left: auto;
      display: inline-block;
      margin-left: 10px; }
  .tu-form-item.is-required:not(.is-no-asterisk) > .tu-form-item__label:before,
  .tu-form-item.is-required:not(.is-no-asterisk) .tu-form-item__label-wrap > .tu-form-item__label:before {
    content: "*";
    color: #f56c6c;
    margin-right: 4px; }
  .tu-form-item.is-error .tu-input__inner,
  .tu-form-item.is-error .tu-textarea__inner {
    position: relative; }
